import React from "react";
import PropTypes from "prop-types";
import Box from "reusecore/src/elements/Box";
import Text from "reusecore/src/elements/Text";
import Heading from "reusecore/src/elements/Heading";
import FeatureBlock from "../../../components/FeatureBlock";
import data from "../../../data/App/FeatureSection";
import Container from "../../../components/UI/Container";
import PaySectionWrapper from "./featureSection.style";
import { ButtonWrapper } from "./featureSection.style";
import Button from "reusecore/src/elements/Button";
import Link from "next/link";
import { white } from "ansi-colors";

const PaySection = ({
  row,
  col,
  sectionHeader,
  sectionTitle,
  sectionSubTitle,
  featureTitle,
  featureDescription,
  iconStyle,
  contentStyle,
  blockWrapperStyle,
  featureStyle
}) => {
  return (
    <PaySectionWrapper id="paybutton" >
      <Container >
        <ButtonWrapper  style={contentStyle}>
          <a href="#services"  
          style = {{
            display: "flex",
            width: "100%",
            height: "100%",
            backgroundColor: "blue",
            borderRadius: 5,
            justifyContent: "center",
            alignItems: "center"
          }}
          >
            <span
            style={{
              color:"white"
            }}>hello</span>
              
           
          </a>
        </ButtonWrapper>
        <Box style={sectionHeader}>
          <Heading {...sectionTitle} />
        </Box>
      </Container>
    </PaySectionWrapper>
  );
};

// FeatureSection style props
PaySection.propTypes = {
  sectionHeader: PropTypes.object,
  contentStyle: PropTypes.object,
  row: PropTypes.object,
  col: PropTypes.object,
  sectionTitle: PropTypes.object,
  sectionSubTitle: PropTypes.object,
  featureTitle: PropTypes.object,
  featureDescription: PropTypes.object
};

// FeatureSection default style
PaySection.defaultProps = {
  // section header default style
  sectionHeader: {
    // mb: ['30px', '30px', '30px', '56px'],
    display: "flex",
    alignItems: "center",
    width: "320px",
    height: "145px",
    border: "1px solid",
    borderTopStyle: "none",
    borderColor: "#d9d9da",
    borderRadius: "0px 0px 5px 5px",
    padding: "45px",
    marginLeft: "auto",
    marginRight: "auto",
    
  },
  contentStyle: {
    minWidth: "273px",
    height: "30px",
    display: "flex",
    marginLeft: "auto",
    marginRight: "auto"
  },
  featureStyle: {},
  featureTitle: {
    fontSize: ["18px", "20px"],
    fontWeight: "400",
    color: "#0f2137",
    lineHeight: "1.5",
    mb: ["10px", "10px", "10px", "20px"],
    letterSpacing: "-0.020em"
  },

  // sub section default style
  sectionSubTitle: {
    content: "OUR SERVICES",
    as: "span",
    display: "block",
    textAlign: "center",
    fontSize: "14px",
    letterSpacing: "0.13em",
    fontWeight: "700",
    color: "#1a73e8",
    mb: "10px",
    textAlign: ["center"]
  },
  // section title default style

  sectionTitle: {
    padding: "50px",
    content: "0765453467",
    textAlign: "center",
    fontSize: ["10px", "14px", "14px", "14px", "16px"],
    fontWeight: "700",
    color: "#0f2137",
    width: "100%",
    letterSpacing: "-0.025em",
    mb: "0",
    textAlign: ["center"]
  },
  // feature row default style
  row: {
    flexBox: true,
    flexWrap: "wrap"
  },
  // feature col default style
  col: {
    width: [1, 1 / 2, 1 / 3, 1 / 3, 1 / 3]
  },
  // feature block wrapper default style
  blockWrapperStyle: {
    p: ["20px", "20px", "20px", "40px"]
  },
  // feature icon default style
  iconStyle: {
    width: "84px",
    height: "84px",
    m: "0 auto",
    borderRadius: "50%",
    bg: "#fff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "36px",
    color: "#29cf8a",
    overflow: "hidden",
    mb: ["20px", "20px", "20px", "30px"],
    border: "1px solid rgba(36, 74, 117,0.1)"
  },
  // feature content default style

  // feature title default style

  // feature description default style
  featureDescription: {
    fontSize: ["14px", "15px"],
    lineHeight: "1.75",
    color: "#343d48"
  }
};

export default PaySection;
